class Observable {
    constructor() {
        this._observers = [];
    }
    register(func) {
        this._observers.push(func);
    }
    unregister(func) {
        this._observers = this._observers.filter((o) => {
            return o !== func;
        });
    }
    notify(prop, prev) {
        this._observers.forEach((o) => {
            o(prop, prev);
        });
    }
}
export default Observable;
